import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
// import logo from './logo.svg';
import './App.css';
import Landin from './components/Landing';

function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <Switch>
          <Route path="/"/>;
          <Route exact path="/" component={Landin} />
        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
